import React from 'react';
import FontAwesome from 'react-fontawesome';
import './MovieInfoBar.css';
import {calcTime, convertMoney} from '../../../helpers.js';

const MovieInfoBar = (props) => {
  return (
    <div className="rmdb-movieinfobar">
      <div className="rmdb-movieinfobar-content">
        <div className="rmdb-movieinfobar-content-col">
          <FontAwesome className="fa-time" name="clock-o" size="1x"/>
          <span className="rmdb-movieinfobar-info">Movie Duration : <br/>{calcTime(props.time)}</span>
        </div>
        <div className="rmdb-movieinfobar-content-col">
          <FontAwesome className="fa-budget" name="money" size="2x"/>
          <span className="rmdb-movieinfobar-info">Budget: <br/>{convertMoney(props.budget)}</span>
        </div>
        <div className="rmdb-movieinfobar-content-col">
          <FontAwesome className="fa-revenue" name="ticket" size="2x"/>
          <span className="rmdb-movieinfobar-info">Revenue: <br/>{convertMoney(props.revenue)}</span>
        </div>
      </div>
    </div>
  );
}
export default MovieInfoBar;
